<template>
  <el-table
      :data="tableData"
      :row-style= 'rowstyle'
      :show-header="status"
      :cell-style= 'rowstyle'
      height="440"
      style="width: 100%; background-color: transparent !important;">
      <el-table-column
      label=""
      :cell-style= 'rowstyle'
      align='center'
        prop="dataname"
        width="120">
      </el-table-column>
      <el-table-column
        prop="datavalue"
        width="245">
      </el-table-column>
    </el-table>
</template>

<script>
export default {
  data () {
    return {
      tableData: [{
        dataname: ':',
        datavalue: '2019102913:15:42'
      }, {
        dataname: ':',
        datavalue: ''
      }, {
        dataname: ':',
        datavalue: '2;21'
      }, {
        dataname: ':',
        datavalue: ',!'
      }, {
        dataname: ':',
        datavalue: ''
      }, {
        dataname: ':',
        datavalue: '7'
      }, {
        dataname: ':',
        datavalue: '155'
      }, {
        dataname: ':',
        datavalue: ''
      }, {
        dataname: ':',
        datavalue: ''
      }],
      status: false
    }
  },
  props: ['eventInfo'],
  methods: {
    rowstyle ({row, rowIndex}) {
      return 'background-color: transparent; border: 0; color: #88d6ff; row:hover : black'
    },
    loadEvent () {
      this.tableData[0].datavalue = this.eventInfo.Fssj
      this.tableData[1].datavalue = this.eventInfo.Category
      this.tableData[2].datavalue = this.eventInfo.Fsdd
      this.tableData[3].datavalue = this.eventInfo.Content
      this.tableData[4].datavalue = this.eventInfo.Source
      this.tableData[5].datavalue = this.eventInfo.Category
      this.tableData[6].datavalue = this.eventInfo.Yxfw
      this.tableData[7].datavalue = this.eventInfo.Category
      this.tableData[8].datavalue = this.eventInfo.Content
      this.tableData[9].datavalue = this.eventInfo.Source
    }
  },
  mounted () {
    // this.loadEvent()
  }
}
</script>

<style lang="stylus" scoped>
el-table{
  background : transparent !important;
}
.el-table::before{
  height : 0;
}
</style>



